.home {
    display: flex;
    width: 100%;
    /* height: 100vh; */
    padding-top: 6%;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: center;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3rem;
}

.info p {
    color: #64ffdb5f;
    font-weight: 300;
    font-size: 14px;
}

.info h1 {
    font-size: 50px;
    margin: 3px 0;
    color: var(--color-white);
}

.download-btn {
    font-size: 17px;
    background-color: transparent;
    color: var( --color-primary);
    border: 1px solid var( --color-primary);
    margin-right: 1.5rem;
    transition: var(--transition);
    cursor: pointer;
    border: 1px solid var(--color-primary);
    -webkit-tap-highlight-color: transparent;
}

.download-btn:hover {
    background-color: var(--color-white);
    color: black;
    border-color: transparent;
}


.hello-btn {
    font-size: 17px;
    color: rgba(0, 0, 0, 0.681);
    background-color: var( --color-primary);
    transition: var(--transition);
    cursor: pointer;
    border: 1px solid var(--color-primary);
    -webkit-tap-highlight-color: transparent;
}

.hello-btn:hover {
    background-color: var(--color-white);
    color: black;
    border-color: transparent;
}

.img-container {
    margin-top: 3rem;
    width: 21rem;
    height: 30rem;
    background-color: var(--color-primary);
    border-top-left-radius: 13rem;
    border-top-right-radius: 13rem;
    background: linear-gradient(to bottom, #4db5ff8e, transparent );
    padding: 0 1rem;
    position: relative;
}

/* .img-container img {
    position: absolute;
    bottom: 0;
    width: 19rem;
    background-position: center;
    background-size: cover;
} */
.img-container img {
    position: absolute;
    bottom: 2.5rem;
    left: -5rem;
    width: 28rem;
    background-position: center;
    background-size: cover;
}

/* ==================== MEDIA QUERIES (Small DEVICES) ==================== */
@media screen and (max-width: 600px) {
    .info h1 {
        font-size: 35px;
    }

    .info p {
        font-weight: 300;
        color: #64ffdb9d
    }
}

/* ==================== MEDIA QUERIES (very Small DEVICES) ==================== */
@media screen and (max-width: 350px) {
    .info h1 {
        font-size: 30px;
    }

    .download-btn {
        margin-right: 0.7rem;   
    }

    .img-container {
        width: 18rem;
        height: 28rem;
        padding: 0 1rem;
    }
    
    .img-container img {
        position: absolute;
        bottom: 0;
        width: 16rem;
    }
}